nner,\n\t&[type=button]::-moz-focus-inner,\n\t&[type=submit]::-moz-focus-inner,\n\t&::-moz-focus-inner,\n\t&[type=file] > [type=button]::-moz-focus-inner {\n\t\tborder: 0;\n\t\tpadding: 0;\n\t}\n}\n\n// Reset margins of last atom in a molecule or an organism\n%reset-last-margin {\n    & > :last-child {\n        margin-bottom: 0;\n    }\n}\n\n// A link that looks and acts like the text it is contained within\n%unstyled-link {\n    color: inherit;\n    text-decoration: inherit;\n    cursor: inherit;\n\n    &:active,\n    &:focus {\n        outline: none;\n    }\n}\n\n// If a large link is used as a last element in any organism\n%reset-last-link-large-margin {\n    margin-bottom:0;\n}\n\n","// Clearfix, For clearing floats like a boss h5bp.com/q\n%clearfix {\n\t*zoom: 1;\n\n\t&:before,\n\t&:after {\n\t\tdisplay: table;\n\t\tcontent: '';\n\t\tline-height: 0;\n\t}\n\n\t&:after {\n\t\tclear: both;\n\t}\n}\n\n// Clear element\n.clear {\n\t@extend %ir;\n\tclear: both;\n\tdisplay: block;\n\toverflow: hidden;\n\tvisibility: hidden;\n\twidth: 0;\n\theight: 0;\n}\n","// Hidden element\n.hidden {\n\tdisplay: none;\n}\n\n// CSS image replacement, Source: https://github.com/h5bp/html5-boilerplate/commit/aa0396eae757\n.ir,\n%ir {\n\tfont: 0/0 a;\n\tcolor: transparent;\n\ttext-shadow: none;\n\tbackground-color: transparent;\n\tborder: 0;\n}\n","%fontPrimary {\n    font-family: $fontPrimary;\n    .fonts--stage-one & {\n        font-family: $fontPrimaryStageOne;\n    }\n\n    .fonts--stage-two & {\n        font-family: $fontPrimaryStageTwo;\n    }\n}\n\n%fontSecondary {\n    font-family: $fontSecondary;\n    .fonts--stage-one & {\n        font-family: $fontSecondaryStageOne;\n    }\n\n    .fonts--stage-two & {\n        font-family: $fontSecondaryStageTwo;\n    }\n}\n","// Building unit\n$buildingUnit:                  12px;\n\n// Source Paths\n$pathFont: \t\t\t\t\t\t'../../static/fonts/';\n$pathImage: \t\t\t\t\t'../../static/media/images/';\n$fontLineHeight:                1.5rem;\n\n\n// Fonts\n$fontArial: \t\t\t\t\tArial, \"Helvetica Neue\", Helvetica, sans-serif;\n$fontPrimary: \t\t\t\t\t$fontArial;\n$fontSecondary: \t\t        $fontArial;\n\n$fontPrimaryStageOne:           'univers-init', $fontArial;\n$fontSecondaryStageOne: \t\t'fugue-init', $fontArial;\n\n$fontPrimaryStageTwo:           'univers', $fontArial;\n$fontSecondaryStageTwo: \t\t'fugue', $fontArial;\n\n// Font weights\n$fontWeights: (\n    thin:  \t\t\t\t\t\t100,\n    ultralight:  \t\t\t\t200,\n    light:  \t\t\t\t\t300,\n    regular:  \t\t\t\t\t400,\n    medium:  \t\t\t\t\t500,\n    semibold:  \t\t\t\t\t600,\n    extrabold:  \t\t\t\t800,\n    black:  \t\t\t\t\t900\n);\n\n// Font sizes\n/*\n* Because we have a primary and secondary font family, we need to define both\nbecause both families serve another purpose. Therefore the line-heights will be different for both families.\n*/\n$fontSizes: (prim-xxs: 10, prim-xs:12, prim-s:14, prim-m:16, prim-l:18, sec-xxs: 16, sec-xs: 18, sec-s:20, sec-m: 24, sec-l: 30, sec-xl: 48, sec-xxl: 60);\n$lineHeights: (prim-xxs: 15, prim-xs:18, prim-s:21, prim-m:24, prim-l:30, sec-xxs:24, sec-xs:24, sec-s:24, sec-m: 30, sec-l: 36, sec-xl: 48, sec-xxl: 60);\n\n/*\n* Offset typography to fit on baseline, this will create the vertical rhythm\n*/\n$offsetTop: (prim-xxs: -8, prim-xs:-10, prim-s:-2, prim-m:-5, prim-l: -8, sec-xxs:6, sec-xs:6, sec-s:5, sec-m: 1, sec-l: -5, sec-xl: -5, sec-xxl: -4);\n$offsetBottom: (prim-xxs: 0, prim-xs:18, prim-s:23, prim-m:29, prim-l: 38, sec-xxs:24, sec-xs:24, sec-s:31, sec-m: 53, sec-l: 59, sec-xl: 23, sec-xxl: 64);\n\n// Icon sizes\n$iconSize: $buildingUnit * 1.5;\n$iconSizeLarge: $buildingUnit * 2;\n\n// Tints\n$tintHoverFocus:                8%; // This is the equivalent of the grayscale of $colorGrayLight (which is: 8% black)\n$tintActive:                    4%; // This is the equivalent of the grayscale of $colorGrayLightExtra (which is: 4% black)\n$tintInactive:                  50%;\n\n// Colors\n$colorMain:                     #0CA9E3;\n$colorError:\t\t\t\t\t#EA5354;\n$colorLike:\t\t\t\t\t\t#EA5354;\n$colorOnline: \t\t\t\t\t#87D128;\n$colorNotification: \t\t\t#CC001A;\n\n// Grayscales\n$colorBlack: \t\t\t\t\t#000000;\n$colorGrayDark:\t\t\t\t\t#7F7F7F;\n$colorGrayMedium:               #B2B2B2;\n$colorGray:\t\t\t\t        #E3E3E3;\n$colorGrayLight:\t\t\t\t#EAEAEA;\n$colorGrayExtraLight:\t\t    #F5F5F5;\n$colorWhite: \t\t\t\t\t#FFFFFF;\n\n// Social Media Colors\n$colorFacebook:\t\t\t\t\t#3b5998;\n$colorTwitter:\t\t\t\t\t#00b6f1;\n$colorGooglePlus:\t\t\t\t#df4a32;\n$colorLinkedIn:\t\t\t\t\t#007bb6;\n$colorWhatsapp:\t\t\t\t\t#43d854;\n\n// Color applications\n$colorButton:                   $colorMain;\n$colorButtonHover:              lighten($colorButton, $tintHoverFocus);\n$colorIconButton:               $colorWhite;\n$colorNavInit:                  $colorBlack;\n$colorBgGray:                   darken($colorWhite, 2%);\n\n\n// Colors accessibility\n\n// Overlays\n$colorOverlayImage:           rgba($colorBlack, 0.3);\n\n//Animations\n$defaultDuration: .3s;\n$defaultEasing: cubic-bezier(.23,1,.32,1);\n\n//Icons\n$defaultIconSize: 1em;\n\n//Header\n$headerHeight: $buildingUnit * 5;\n","$focusBorderSize: 2px;\n$colorMainAccessible: #007faa; /* Picked an accessible color from colorsafe.co to try out some time */\n\n%focus {\n        color: $colorWhite;\n        background-color: lighten($colorMain, $tintHoverFocus);\n}\n\n%focus-outline {\n        outline: $focusBorderSize solid lighten($colorMain, $tintHoverFocus);\n}\n","%box-shadow {\n    box-shadow: 0 10px 20px rgba(0, 0, 0, .19), 0 3px 3px rgba(0, 0, 0, .23);\n}\n","/*! normalize.css v7.0.0 | MIT License | github.com/necolas/normalize.css */\n\n/* Document\n   ========================================================================== */\n\n/**\n * 1. Correct the line height in all browsers.\n * 2. Prevent adjustments of font size after orientation changes in\n *    IE on Windows Phone and in iOS.\n */\n\nhtml {\n    line-height: 1.15; /* 1 */\n    -ms-text-size-adjust: 100%; /* 2 */\n    -webkit-text-size-adjust: 100%; /* 2 */\n}\n\n/* Sections\n   ========================================================================== */\n\n/**\n * Remove the margin in all browsers (opinionated).\n */\n\nbody {\n    margin: 0;\n}\n\n/**\n * Add the correct display in IE 9-.\n */\n\narticle,\naside,\nfooter,\nheader,\nnav,\nsection {\n    display: block;\n}\n\n/**\n * Correct the font size and margin on `h1` elements within `section` and\n * `article` contexts in Chrome, Firefox, and Safari.\n */\n\nh1 {\n    font-size: 2em;\n    margin: 0.67em 0;\n}\n\n/* Grouping content\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n * 1. Add the correct display in IE.\n */\n\nfigcaption,\nfigure,\nmain { /* 1 */\n    display: block;\n}\n\n/**\n * Add the correct margin in IE 8.\n */\n\nfigure {\n    margin: 1em 40px;\n}\n\n/**\n * 1. Add the correct box sizing in Firefox.\n * 2. Show the overflow in Edge and IE.\n */\n\nhr {\n    box-sizing: content-box; /* 1 */\n    height: 0; /* 1 */\n    overflow: visible; /* 2 */\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\npre {\n    font-family: monospace, monospace; /* 1 */\n    font-size: 1em; /* 2 */\n}\n\n/* Text-level semantics\n   ========================================================================== */\n\n/**\n * 1. Remove the gray background on active links in IE 10.\n * 2. Remove gaps in links underline in iOS 8+ and Safari 8+.\n */\n\na {\n    background-color: transparent; /* 1 */\n    -webkit-text-decoration-skip: objects; /* 2 */\n}\n\n/**\n * 1. Remove the bottom border in Chrome 57- and Firefox 39-.\n * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.\n */\n\nabbr[title] {\n    border-bottom: none; /* 1 */\n    text-decoration: underline; /* 2 */\n    text-decoration: underline dotted; /* 2 */\n}\n\n/**\n * Prevent the duplicate application of `bolder` by the next rule in Safari 6.\n */\n\nb,\nstrong {\n    font-weight: inherit;\n}\n\n/**\n * Add the correct font weight in Chrome, Edge, and Safari.\n */\n\nb,\nstrong {\n    font-weight: bolder;\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\ncode,\nkbd,\nsamp {\n    font-family: monospace, monospace; /* 1 */\n    font-size: 1em; /* 2 */\n}\n\n/**\n * Add the correct font style in Android 4.3-.\n */\n\ndfn {\n    font-style: italic;\n}\n\n/**\n * Add the correct background and color in IE 9-.\n */\n\nmark {\n    background-color: #ff0;\n    color: #000;\n}\n\n/**\n * Add the correct font size in all browsers.\n */\n\nsmall {\n    font-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n\nsub,\nsup {\n    font-size: 75%;\n    line-height: 0;\n    position: relative;\n    vertical-align: baseline;\n}\n\nsub {\n    bottom: -0.25em;\n}\n\nsup {\n    top: -0.5em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n */\n\naudio,\nvideo {\n    display: inline-block;\n}\n\n/**\n * Add the correct display in iOS 4-7.\n */\n\naudio:not([controls]) {\n    display: none;\n    height: 0;\n}\n\n/**\n * Remove the border on images inside links in IE 10-.\n */\n\nimg {\n    border-style: none;\n}\n\n/**\n * Hide the overflow in IE.\n */\n\nsvg:not(:root) {\n    overflow: hidden;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers (opinionated).\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n    font-family: sans-serif; /* 1 */\n    font-size: 100%; /* 1 */\n    line-height: 1.15; /* 1 */\n    margin: 0; /* 2 */\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput { /* 1 */\n    overflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect { /* 1 */\n    text-transform: none;\n}\n\n/**\n * 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`\n *    controls in Android 4.\n * 2. Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\nhtml [type=\"button\"], /* 1 */\n[type=\"reset\"],\n[type=\"submit\"] {\n    -webkit-appearance: button; /* 2 */\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n    border-style: none;\n    padding: 0;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n    outline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n    padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n    box-sizing: border-box; /* 1 */\n    color: inherit; /* 2 */\n    display: table; /* 1 */\n    max-width: 100%; /* 1 */\n    padding: 0; /* 3 */\n    white-space: normal; /* 1 */\n}\n\n/**\n * 1. Add the correct display in IE 9-.\n * 2. Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n    display: inline-block; /* 1 */\n    vertical-align: baseline; /* 2 */\n}\n\n/**\n * Remove the default vertical scrollbar in IE.\n */\n\ntextarea {\n    overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10-.\n * 2. Remove the padding in IE 10-.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n    box-sizing: border-box; /* 1 */\n    padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n    height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n    -webkit-appearance: textfield; /* 1 */\n    outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding and cancel buttons in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-cancel-button,\n[type=\"search\"]::-webkit-search-decoration {\n    -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n    -webkit-appearance: button; /* 1 */\n    font: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in IE 9-.\n * 1. Add the correct display in Edge, IE, and Firefox.\n */\n\ndetails, /* 1 */\nmenu {\n    display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n    display: list-item;\n}\n\n/* Scripting\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n */\n\ncanvas {\n    display: inline-block;\n}\n\n/**\n * Add the correct display in IE.\n */\n\ntemplate {\n    display: none;\n}\n\n/* Hidden\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10-.\n */\n\n[hidden] {\n    display: none;\n}\n",".content-container {\n    will-change: transform;\n}\n","/**\n * Font Face\n * @param {string} $fontName - Name of the font\n * @param {string} $folderName - Subfolder inside the fonts folder\n * @param {string} $fileName - Name of the font-file\n * @param {string} $fontWeight (normal) - Font weight value\n * @param {string} $fontStyle (normal) - Font style value\n */\n@mixin font-face($fontName, $folderName, $fileName, $fontWeight: normal, $fontStyle: normal){\n    @font-face {\n        font-family: $fontName;\n        src: url('#{$pathFont}#{$folderName}/#{$fileName}.woff2') format('woff2'),\n            url('#{$pathFont}#{$folderName}/#{$fileName}.woff') format('woff');\n        font-weight: $fontWeight;\n        font-style: $fontStyle;\n    }\n}\n\n/**\n * CSS Background\n * @param {string} $name ($spriteDefault) - Background image, without the image path\n * @param {string} $repeat (no-repeat) - Background repeat\n * @param {position} $left (0) - Background position X\n * @param {position} $top (0) - Background position Y\n * @param {color} $color (transparent) - Background color\n * @param {string} $attachment (scorll) - Background attachment\n */\n@mixin background($name: '', $repeat: no-repeat, $left: 0, $top: 0, $color: transparent, $attachment: scroll){\n\tbackground: $color url('#{$pathImage}#{$name}') $repeat $left $top $attachment;\n}\n\n// Text overflow, Requires inline-block or block for proper styling\n@mixin text-overflow {\n\toverflow: hidden;\n\ttext-overflow: ellipsis;\n\twhite-space: nowrap;\n}\n\n// Disable the use of Ligatures in browsers\n@mixin no-ligatures {\n\tfont-variant-ligatures: no-common-ligatures;\n\tfont-feature-settings: \"liga\" 0;\n}\n\n/**\n * Creates an arrow\n * @param {string} $direction (up) - Arrow direction: up, down, left or right\n * @param {color} $color (#000) - Arrow color\n * @param {size} $size (10px) - Size of the opposite side of the arrow\n * @param {number} $stretch (1) - Amount of the stretch of the arrow\n * @param {boolean} $exclude-base (false) - Remove the default arrow styling (usefull if you want to modify the direction of the arrow on rollover\n */\n@mixin arrow($direction: up, $color: #000, $size: 10px, $stretch: 1, $excludeBase: false) {\n\n\t@if $excludeBase == false {\n\t\tdisplay: inline-block;\n\t\tvertical-align: middle;\n\t\twidth: 0;\n\t\theight: 0;\n\t\tfont: 0/0 serif;\n\t}\n\n\t$size: round($size / 2);\n\t$longSize: $size * $stretch;\n\n\t@if $direction == down {\n\t\tborder-top: $longSize solid #{$color};\n\t\tborder-left: $size solid transparent;\n\t\tborder-right: $size solid transparent;\n\t\tborder-bottom: 0;\n\t} @else if $direction == left {\n\t\tborder-top: $size solid transparent;\n\t\tborder-left: 0;\n\t\tborder-right: $longSize solid #{$color};\n\t\tborder-bottom: $size solid transparent;\n\t} @else if $direction == right {\n\t\tborder-top: $size solid transparent;\n\t\tborder-left: $longSize solid #{$color};\n\t\tborder-right: 0;\n\t\tborder-bottom: $size solid transparent;\n\t} @else {\n\t\tborder-top: 0;\n\t\tborder-left: $size solid transparent;\n\t\tborder-right: $size solid transparent;\n\t\tborder-bottom: $longSize solid #{$color};\n\t}\n\n}\n\n/**\n * Outputs a ratio holder\n * @param {number} $width - the width of the desired (original) size\n * @param {number} $height - the height of the desired (original) size\n * @param {string} $type (before) - type of psuedo element\n * @param {boolean} $firstDescendant (true) - same element or first descendant\n */\n@mixin ratioHolder($width, $height, $type: before, $firstDescendant: true){\n\t@if $type == before {\n\t\t@if $firstDescendant == true {\n\t\t\t&:before {\n\t\t\t\tcontent: '';\n\t\t\t\tdisplay: block;\n\t\t\t\tpadding-top: percentage($height / $width);\n\t\t\t}\n\t\t}\n\t\t@else{\n\t\t\tcontent: '';\n\t\t\tdisplay: block;\n\t\t\tpadding-top: percentage($height / $width);\n\t\t}\n\t}\n\t@else if $type == after {\n\t\t@if $firstDescendant == true {\n\t\t\t&:after {\n\t\t\t\tcontent: '';\n\t\t\t\tdisplay: block;\n\t\t\t\tpadding-top: percentage($height / $width);\n\t\t\t}\n\t\t}\n\t\t@else {\n\t\t\tcontent: '';\n\t\t\tdisplay: block;\n\t\t\tpadding-top: percentage($height / $width);\n\t\t}\n\t}\n}\n\n/**\n * Object fit for images (can be used in combination with https://github.com/bfred-it/object-fit-images\n * @param {string} $fit - Type of fit cover/contain etc.\n * @param {string} $position - almost the same as background-position\n */\n@mixin object-fit($fit: fill, $position: null){\n\tobject-fit: $fit;\n\t@if $position {\n\t\tobject-position: $position;\n\t\tfont-family: 'object-fit: #{$fit}; object-position: #{$position}';\n\t} @else {\n\t\tfont-family: 'object-fit: #{$fit}';\n\t}\n}\n\n// Hide only visually, but have it available for screenreaders: h5bp.com/v\n@mixin visually-hidden {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    overflow: hidden;\n    clip: rect(1px, 1px, 1px, 1px);\n}\n@mixin reset-visually-hidden {\n    position: relative;\n    width: auto;\n    height: auto;\n    overflow: initial;\n    clip: initial;\n}\n","// Fonts\n\n//@include font-face('woff/woff2 only', 'family_name', 'sub_folder', 'filename_no_ext', 'font_weight', 'font_style');\n\n// Viewcounter for myfonts.\n// Should only be implemented for production\n//\n//npm i@import url(\"//hello.myfonts.net/count/321a0b\");\n\n\n@include font-face('fugue-init', 'fugue', 'fugue-ultimaker-regular');\n@include font-face('univers-init', 'univers', 'univers-light', 100);\n\n@include font-face('fugue', 'fugue', 'fugue-ultimaker-regular');\n@include font-face('univers', 'univers', 'univers-light', 100);\n@include font-face('univers', 'univers', 'univers-light-italic', 100, italic);\n@include font-face('univers', 'univers', 'univers-regular', 400);\n@include font-face('univers', 'univers', 'univers-regular-italic', 400, italic);\n@include font-face('univers', 'univers', 'univers-bold', 700);\n@include font-face('univers', 'univers', 'univers-bold-italic', 700, italic);\n@include font-face('univers', 'univers', 'univers-black', 900);\n@include font-face('univers', 'univers', 'univers-black-italic', 900, italic);\n","html {\n    box-sizing: border-box;\n}\n*, *:before, *:after {\n    box-sizing: inherit;\n}\n","@each $label, $map in $gridBreakpoints {\n    $selector: map-get($map, selector);\n    $minWidth: map-get($map, minWidth);\n    $maxWidth: map-get($map, maxWidth);\n\n    @media (min-width: #{$minWidth}) and (max-width: #{$maxWidth}) {\n        .hidden--#{$selector} {\n            display: none;\n        }\n    }\n}\n","$triangleWidth: 25px;\n$triangleHeight: 12px;\n\nbutton, .button, input[type=\"button\"], input[type=\"submit\"] {\n    position: relative;\n    display: inline-block;\n\n    user-select: none;\n    appearance: none;\n\n    outline: none;\n    border: none;\n    border-radius: 0;\n\n    padding: ($buildingUnit * 0.75) ($buildingUnit * 2.5);\n\n    * + & {\n       margin-right: $buildingUnit;\n       margin-bottom: $buildingUnit;\n   }\n\n    @extend %fontSecondary;\n    @include font-line-ratio(sec-xs);\n\n    color: $colorWhite;\n    background-color: $colorButton;\n    cursor: pointer;\n\n    @include micro-animate(color, background-color, border-color);\n\n    &:hover {\n        color: $colorWhite;\n        background-color: $colorButtonHover;\n        outline:0;\n    }\n    &:focus {\n        @extend %focus;\n    }\n\n    @include min-tablet() {\n        @include font-line-ratio(sec-s);\n        padding-top: $buildingUnit; padding-bottom: $buildingUnit;\n    }\n}\n\n.button--primary {\n    &:before {\n        content: '';\n        position:absolute;\n        top: 0; right: -$triangleWidth;\n\n        border-style: solid;\n        border-width: 0 $triangleWidth $triangleHeight 0;\n        border-color: transparent transparent $colorMain transparent;\n\n        @include micro-animate(border-color);\n    }\n\n    &:after {\n        content: '';\n        position: absolute;\n        top: $triangleHeight; right: -$triangleWidth;\n        bottom: 0;\n        width: $triangleWidth;\n        background-color: inherit;\n    }\n\n    padding-right: $buildingUnit * 2.5 - $triangleWidth;\n\n    * + & {\n        margin-right: $triangleWidth + $buildingUnit;\n        margin-bottom: $buildingUnit * 3;\n    }\n\n    @include min-tablet {\n        margin-bottom: $buildingUnit * 4;\n    }\n    &:hover, &:focus {\n        &:before {\n            border-color: transparent transparent lighten($colorButton, $tintHoverFocus) transparent;\n        }\n    }\n}\n\n.button--secondary {\n    background: transparent;\n    border: 2px solid $colorMain;\n    padding-top:  ($buildingUnit * 0.75) - 2;\n    padding-bottom:  ($buildingUnit * 0.75) - 2;\n    color: $colorMain;\n\n    @include min-tablet() {\n        padding-top: $buildingUnit - 2; padding-bottom: $buildingUnit - 2;\n    }\n    &:hover, &:focus {\n        border: 2px solid transparent;\n    }\n}\n\n.button--close {\n    position: absolute;\n    top:0;\n    right: 0;\n}\n","// Get the 'font-size/line-height' ratio according to demanded font-size\n@mixin font-line-ratio($sizeName) {\n    $sizeValue: map-get( get-font-specs($sizeName), size );\n    $lineValue: map-get( get-font-specs($sizeName), height );\n\n    font-size: $sizeValue + px;\n    line-height: $lineValue + px;\n}\n\n// Create vertical rhythm without the 'font-size/line-height' ratio\n@mixin font-rhythm($sizeName) {\n    $offsetTopValue: map-get( get-font-specs($sizeName), top);\n    $offsetBottomValue: map-get( get-font-specs($sizeName), bottom);\n\n    margin-top: $offsetTopValue + px;\n    margin-bottom: $offsetBottomValue + px;\n}\n\n//Combine the 'font-size/line-height' ratio with the offsets to create a vertical rhythm\n@mixin font-ratio-rhythm($sizeName) {\n    @include font-line-ratio($sizeName);\n    @include font-rhythm($sizeName);\n}\n","/*\n * @param {string} $animateProp - Name of the animated property or multiple properties\n */\n\n@mixin micro-animate($animateProp...) {\n    $transition: '';\n\n    @each $prop in $animateProp {\n        $transition: $transition + ' ' + $prop + ' ' + $defaultDuration + ' ' + $defaultEasing + ', ';\n    }\n\n    $transition: str-slice($transition, 1, -3);\n    transition: #{$transition};\n}\n","/*\n *  Mixins to support media queries\n *  Remark: they are not generated but written out to support autocomplete in your favourite editor.\n */\n\n@mixin min-mobile {\n    @media (min-width: $mobileWidth) {\n        @content;\n    }\n}\n\n@mixin mobile {\n    @media (min-width: $mobileWidth) and (max-width: $tabletWidth - 1) {\n        @content;\n    }\n}\n\n@mixin max-mobile {\n    @media (max-width: $mobileXlWidth - 1) {\n        @content;\n    }\n}\n\n@mixin min-mobile-xl {\n    @media (min-width: $mobileXlWidth) {\n        @content;\n    }\n}\n\n@mixin max-mobile-xl {\n    @media (max-width: $tabletWidth - 1) {\n        @content;\n    }\n}\n\n@mixin min-tablet {\n    @media (min-width: $tabletWidth) {\n        @content;\n    }\n}\n\n@mixin tablet {\n    @media (min-width: $tabletWidth) and (max-width: $desktopWidth - 1px) {\n        @content;\n    }\n}\n\n@mixin max-tablet {\n    @media (max-width: $desktopWidth - 1) {\n        @content;\n    }\n}\n\n@mixin min-desktop {\n    @media (min-width: $desktopWidth) {\n        @content;\n    }\n}\n\n@mixin desktop {\n    @media (min-width: $desktopWidth) and (max-width: $desktopHdWidth - 1px) {\n        @content;\n    }\n}\n\n@mixin min-desktop-hd {\n    @media (min-width: $desktopHdWidth) {\n        @content;\n    }\n}\n",".drawer {\n    position: relative;\n    z-index: 1010;\n}",".faux-vertical-scroll {\n    position: relative;\n    max-width: 100%;\n    overflow-x: hidden;\n    overflow-y: visible;\n\n    .faux-vertical-scroll__bar {\n        position: absolute;\n        z-index: 1;\n        bottom: 0;\n        content: '';\n        display: block;\n        height: 1px;\n        background-color: $colorMain;\n\n        transition: opacity 1s;\n    }\n}\n\n.faux-vertical-scroll__content {\n    transform: translateX(0);\n}\n\n","// Root element\nhtml {\n    @include font-line-ratio(prim-s);\n    font-weight: 100;\n    font-family: $fontPrimary;\n\n    &.fonts--stage-one {\n        font-family: $fontPrimaryStageOne;\n    }\n\n    &.fonts--stage-one {\n        font-family: $fontPrimaryStageTwo;\n    }\n\n    color: $colorBlack;\n    background: $colorWhite;\n\n    text-rendering: optimizeLegibility;\n    font-feature-settings: \"kern\";\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n\n    @include min-tablet() {\n        @include font-line-ratio(prim-m);\n    }\n}\n\n// Body\n.copy--intro {\n    @include font-ratio-rhythm(prim-m);\n\n    @include min-tablet() {\n        @include font-ratio-rhythm(prim-l);\n    }\n}\n\np {\n    @include font-rhythm(prim-s);\n\n    @include min-tablet() {\n        @include font-rhythm(prim-m);\n    }\n}\n\nstrong {\n    display: block;\n    @extend p;\n\n    p & {\n        display: inline;\n    }\n}\n\nq {\n    @extend h3;\n    display: block;\n    text-align: center;\n    color: $colorGrayDark;\n}\n\n.copy--small {\n    @include font-ratio-rhythm(prim-s);\n}\n\nsmall {\n    @include font-line-ratio(prim-xs);\n    display: block;\n\n    font-weight: 500;\n    color: inherit;\n\n    @include min-tablet() {\n        @include font-line-ratio(prim-xs);\n    }\n}\n\n// Headlines\nh1,\nh2,\nh3 {\n    @extend %fontSecondary;\n    font-weight: 400;\n}\n\n// Vertical rhythm\nh1,\n.headline--primary {\n    @include font-ratio-rhythm(sec-l);\n\n    @include min-tablet() {\n        @include font-ratio-rhythm(sec-xxl);\n    }\n}\n\nh2,\n.headline--secondary {\n    @include font-ratio-rhythm(sec-m);\n\n    @include min-tablet() {\n        @include font-ratio-rhythm(sec-l);\n    }\n}\n\nh3,\n.headline--tertiary {\n    @include font-ratio-rhythm(sec-xs);\n\n    @include min-tablet() {\n        @include font-ratio-rhythm(sec-s);\n    }\n}\n\nh4,\nh5,\nh6,\n.headline--quaternary,\n.headline--quinary,\n.headline--senary,\nstrong {\n    @extend %fontPrimary;\n    font-weight: 600;\n}\n\nh4, strong {\n    @include font-line-ratio(prim-s);\n\n    margin-top: -2px;\n    margin-bottom: 2px;\n\n    @include min-tablet() {\n        @include font-line-ratio(prim-m);\n\n        margin-top: -5px;\n        margin-bottom: 5px;\n    }\n}\n\nh5 {\n    @extend .copy--small;\n    margin-top: -2px;\n    margin-bottom: 5px;\n}\n\nh6 {\n    @extend small;\n    margin-top: 0;\n    margin-bottom: 0;\n}\n",".icon {\n    width: $defaultIconSize;\n    height: $defaultIconSize;\n}\n","/* ------------------ Animations ------------------ */\n@keyframes arrowLineAnimate {\n    0% {\n        transform: translate(0, 0) scaleX(0);\n    }\n    12.5% {\n        transform: translate(0, 0) scaleX(1.075);\n    }\n    37.5% {\n        transform: translate(0, 0) scaleX(1.075);\n    }\n    50% {\n        transform: translate(-100%, 0) scaleX(1.075);\n    }\n    50.1% {\n        transform: translate(0, 0) scaleX(0);\n    }\n}\n\n@keyframes arrowLineAnimateRotated-2 {\n    10% {\n        transform: rotate(45deg) scaleX(0) translate(-5.65px, -5.15px);\n    }\n    15% {\n        transform: rotate(45deg) scaleX(.425) translate(-5.65px, -5.15px);\n    }\n    45% {\n        transform: rotate(45deg) scaleX(.425) translate(-5.65px, -5.15px);\n    }\n    60% {\n        transform: rotate(45deg) scaleX(0) translate(-5.65px, -5.15px);\n    }\n}\n\n@keyframes arrowLineAnimateRotated-3 {\n    10% {\n        transform: rotate(-45deg) scaleX(0) translate(-5px, 4.75px);\n    }\n    15% {\n        transform: rotate(-45deg) scaleX(.425) translate(-5px, 4.75px);\n    }\n    45% {\n        transform: rotate(-45deg) scaleX(.425) translate(-5px, 4.75px);\n    }\n    60% {\n        transform: rotate(-45deg) scaleX(0) translate(-5px, 4.75px);\n    }\n}\n\n/* ------------------ Presentation ------------------ */\n$arrowAnimationDuration: ($defaultDuration * 4);\n\n.arrow__mask {\n    transform-origin: center center;\n    transform: scale(2) translateX(3.35px) rotate(45deg);\n}\n\n.arrow__line-1 {\n    transform: scaleX(0);\n    transform-origin: 110px 0%;\n\n    animation: arrowLineAnimate $arrowAnimationDuration ease-in-out infinite;\n}\n\n.arrow__line-2 {\n    transform: scaleX(0);\n    transform-origin: 10px 50%;\n    animation: arrowLineAnimateRotated-2 $arrowAnimationDuration ease-in-out infinite;\n}\n\n.arrow__line-3 {\n    transform: scaleX(0);\n    transform-origin: 10px 50%;\n    animation: arrowLineAnimateRotated-3 $arrowAnimationDuration ease-in-out infinite;\n}\n",".icon-button {\n    @include micro-animate(background-color);\n    @include font-line-ratio(prim-s);\n\n    display: inline-flex;\n    justify-content: flex-end;\n\n    padding: $buildingUnit;\n    height: $buildingUnit * 4;\n\n    color: $colorBlack;\n    background-color: $colorWhite;\n\n    cursor: pointer;\n\n    &:hover {\n        color: $colorBlack;\n        background-color: $colorGrayLight;\n    }\n\n    &--light {\n        &,\n        &:hover {\n            color: $colorWhite;\n            background: rgba($colorWhite, 0.08);\n        }\n    }\n}\n\n.icon-button__icon {\n    font-size: $buildingUnit * 2;\n}\n\n.icon-button__label {\n    margin: 4px ($buildingUnit / 2) 0 $buildingUnit;\n    white-space: nowrap;\n    user-select: none;\n}\n\n.icon-button--small {\n    padding: $buildingUnit / 2;\n    height: $buildingUnit * 3;\n}\n\n.icon-button--large {\n    padding: ($navHeight - 24px) / 2;\n    width: $navHeight;\n    height: $navHeight;\n}\n\n.icon-button--round {\n    border-radius: 50% 50%;\n    height: $buildingUnit * 4;\n    width: $buildingUnit * 4;\n}\n\n.icon-button--drop-shadow{\n    box-shadow: 0 2px 8px 0 rgba(0,0,0,.3);\n}\n\n.icon-button--reversed {\n    display: inline-flex;\n    flex-direction: row-reverse;\n\n    .icon-button__label {\n        margin-left: $buildingUnit / 2;\n        margin-right: $buildingUnit;\n    }\n    .icon-button__icon {\n        margin-left: auto;\n    }\n}\n\n.icon-button--underline {\n    @extend %fontSecondary;\n    @include font-ratio-rhythm(sec-xxs);\n    @include min-tablet() {\n        @include font-ratio-rhythm(sec-xs);\n    }\n\n    display: inline;\n    position: relative;\n    padding-left: $iconSize * (5 / 3);\n    color: $colorBlack;\n\n    &, &:hover {\n        background-color: transparent;\n    }\n\n    .icon-button__icon {\n        position: absolute;\n        top: $buildingUnit;\n        left: $iconSize * (1 / 3);\n        font-size: $iconSize;\n    }\n\n    .icon-button__label {\n        @include micro-animate(background-size);\n\n        margin: 0;\n        width: calc(100% - 0.1px); // solves IE11 issue\n        background-size: 100% 100%;\n        background-repeat: no-repeat;\n        background-position: center bottom;\n        background-image: linear-gradient(transparent calc(100% - 1px), currentColor 10px);\n    }\n    \n    &:hover, &:active, &:visited {\n        .icon-button__label {\n            background-size: 0% 100%;\n        }\n    }\n}\n\n.icon-button--underline-large {\n    @extend .icon-button--underline;\n    @include font-ratio-rhythm(sec-xs);\n    @include min-tablet() {\n        @include font-ratio-rhythm(sec-s);\n    }\n\n    padding-left: $iconSizeLarge *  (5 / 3);\n\n    .icon-button__icon {\n        left: $iconSizeLarge * (1 / 3);\n        font-size: $iconSizeLarge;\n    }\n\n    .icon-button__label {\n        background-image: linear-gradient(transparent calc(100% - 2px), currentColor 10px);\n    }\n}\n","$navHeight: $buildingUnit * 5;\n$navIconHeight: $buildingUnit * 2;\n$navIconWidth: $buildingUnit * 2;\n$mainNavMobileWidth: 200px;\n\n$homeLinkWidth: 120px;\n$homeLinkDesktopHdWidth: 162px\n","ul,\n.list {\n    @include font-ratio-rhythm(prim-s);\n\n    padding-left: 1em;\n\n    @include min-tablet() {\n        @include font-ratio-rhythm(prim-m);\n    }\n}\n\n.list__item {\n        @extend %reset-list-item;\n        padding-left: ($buildingUnit - 2px);\