}\n\ntable tfoot {\n    @include font-line-ratio(prim-xs);\n}\n",".youtube-video {\n    @include ratioHolder(16, 9);\n    position: relative;\n\n    &__frame {\n        display: block;\n        position: absolute;\n        top: 0; left: 0;\n        height: 100%;\n        width: 100%;\n    }\n\n    &--four-three {\n        @include ratioHolder(4, 3);\n    }\n\n    &--eighteen-nine {\n        @include ratioHolder(2, 1);\n    }\n\n    &--square {\n        @include ratioHolder(1, 1);\n    }\n}\n",".block {\n    @extend %reset-last-margin;\n\n    flex: 1;\n    margin-bottom: $buildingUnit;\n}\n","/**\n * Position X,Y,Z (Fallback Top, Left IE)\n * @param {number} $x - X Position\n * @param {number} $y - Y Position\n * @param {number} $z - Z Position\n */\n@mixin position($x:0, $y:0, $z:0) {\n\tposition: absolute;\n\tleft: 0;\n\ttop: 0;\n\n\t@include translate3d($x, $y, $z);\n\n\t.oldie & {\n\t\tleft: $x * 1px;\n\t\ttop: $y * 1px;\n\t}\n}\n\n/**\n * Position\n * @param {number} $top (0) - Top Position\n * @param {number} $right (auto) - Right Position\n * @param {number} $bottom (auto) - Bottom Position\n * @param {number} $left (0) - Left Position\n */\n@mixin pos($top: 0, $right: auto, $bottom: auto, $left: 0) {\n\ttop: $top;\n\tbottom: $bottom;\n\tleft: $left;\n\tright: $right;\n}\n\n/**\n * Offset\n * @param {number} $top (0) - Top Offset\n * @param {number} $left (0) - Left Offset\n */\n@mixin offset($x: 0, $y: 0) {\n\ttop: $y;\n\tleft: $x;\n}\n\n/**\n * Positioning an element absolute in the center\n */\n@mixin abs-center(){\n\tposition: absolute;\n\t@include pos(50%, auto, auto, 50%);\n\ttransform: translate(-50%, -50%);\n}\n\n/**\n * Places an element absolute on top of the entire wrapper\n */\n@mixin coverer {\n\tposition: absolute;\n\t@include pos(0, 0, 0, 0);\n}\n\n/**\n * Set an element to display block and center margin\n */\n@mixin center-block {\n\tdisplay: block;\n\tmargin-left: auto;\n\tmargin-right: auto;\n}\n",".modal {\n    @include pos(0, 0, 0, 0);\n\n    position: fixed;\n    will-change: transform;\n    z-index: 1000;\n\n    .modal__overlay {\n        display: block;\n        height: 100%;\n        will-change: transform;\n        transition: opacity ease('easeOut') 0.2s;\n        opacity: 0.8;\n        background-color: $colorBlack;\n    }\n}\n.modal__content {\n    @include abs-center;\n    z-index: 1010;\n    transform: translate(-50%, -50%) scaleY(1);\n\n    box-sizing: border-box;\n    padding: 20px 40px;\n    max-width: 85vw;\n    max-height: 75vh;\n    background-color: $colorGrayExtraLight;\n\n    overflow: hidden;\n    overflow-y: auto;\n}\n\n.modal__close {\n    @extend .icon-button--light;\n\n    position: absolute;\n    z-index: 1020;\n    top: 0px;\n    right: 0px;\n\n    background-color: transparent;\n    color: $colorWhite;\n}\n\n.modal--video {\n    .modal__content {\n        width: 100%;\n        max-width: $desktopHdWidth - 1;\n        max-height: 100%;\n        padding: 0;\n    }\n}\n",".currency {\n    @extend %fontSecondary;\n    @include font-line-ratio(sec-xs);\n    margin-top: -1px;\n    @include min-tablet {\n        @include font-line-ratio(sec-s);\n    }\n}\n\n.currency__type {\n\n}\n",".price-table {\n    display: table;\n    width: auto;\n    margin-top: $buildingUnit * -1;\n    margin-bottom: $buildingUnit * 1.5;\n}\n\n.price-table--tabs{\n    .price-table__item {\n        @include micro-animate(background-color, color);\n        cursor: pointer;\n\n        padding: $buildingUnit;\n\n        &:hover{\n            color: $colorBlack;\n            outline: 0;\n\n            background-color: darken($colorWhite, $tintHoverFocus);\n        }\n\n        &:focus:not(:hover),\n        &:focus:not(:active) {\n            @extend %focus;\n        }\n\n        &:focus {\n            outline: none;\n            * {\n                color: $colorWhite;\n            }\n        }\n    }\n\n}\n\n.price-table__list {\n    @extend %reset-list;\n    display: flex;\n}\n\n.price-table__item {\n    display: flex;\n    flex: 0 1 auto;\n    flex-direction: row;\n\n    padding: $buildingUnit 0;\n    padding-bottom: 7px;\n\n    background-color: transparent;\n    color: $colorBlack;\n}\n\n.price-table__tagline,\n.price-table__disclaimer {\n    @extend small;\n    color: $colorGrayDark;\n}\n\n.price-table__disclaimer {\n    display: block;\n    margin-top: $buildingUnit * 0.5;\n}\n\n.price-table__item--inactive {\n    color: $colorGrayDark;\n}\n\n.price-table__item--active {\n    background-color: darken($colorWhite, $tintActive);\n}\n\n.price-table__icon {\n    font-size: $buildingUnit * 3;\n    margin-right: $buildingUnit;\n}\n\n.price-table__info {\n    display: flex;\n    flex-direction: column;\n}\n","%disable-flex-grow {\n    [class*=\"flexgrid__cell\"] {\n        flex-grow: 0;\n    }\n}\n\n.container {\n    max-width: $desktopHdWidth;\n    margin: 0 auto;\n    padding: ($buildingUnit * 5) $buildingUnit;\n\n    @include min-tablet {\n        padding: ($buildingUnit * 5) $buildingUnit * 2;\n    }\n\n    @include min-desktop {\n        padding: ($buildingUnit * 5) $buildingUnit * 4;\n    }\n\n    @include min-desktop-hd {\n        padding: ($buildingUnit * 5) $buildingUnit * 5;\n    }\n}\n\n.flexgrid {\n    display: flex;\n    box-sizing: border-box;\n    flex-flow: column wrap;\n    flex: 1 0 auto;\n    width: 100%;\n\n    @include min-mobile {\n        flex-direction: row;\n    }\n}\n\n.flexgrid__cell, \n[class*=\"flexgrid__cell\"] {\n    position: relative;\n    display: block;\n    box-sizing: border-box;\n\n    flex: 0 0 100%;\n    max-width: 100%;\n}\n\n/*\n *  Responsive flexgrid\n *  Generate columns for each of the defined breakpoints\n */\n\n@each $label, $map in $gridBreakpoints {\n    $selector: map-get($map, selector);\n    $minWidth: map-get($map, minWidth);\n\n    @media (min-width: #{$minWidth}) {\n        [class*=\"flexgrid__cell--#{$selector}\"] {\n            //display: flex; /* enables flexgrid cells to have equal heights */\n            flex-basis: auto;\n            max-width: 100%;\n            flex-grow: 1;\n        }\n\n        @for $i from 1 through $gridNumberOfCols {\n            $divider: 100 / ($gridNumberOfCols / $i);\n\n            .flexgrid__cell--#{$selector}-#{$i} {\n                flex-basis: $divider#{'%'};\n                max-width: $divider#{'%'};\n            }\n        }\n    }\n}\n\n.flexgrid--gutter {\n    margin: 0 $gridGutterSizeMobile / -2;\n\n    [class*=\"flexgrid__cell\"] {\n        padding: 0 $gridGutterSizeMobile / 2;\n    }\n\n    @include min-tablet {\n        margin: 0 $gridGutterSize / -2;\n\n        [class*=\"flexgrid__cell\"] {\n            padding: 0 $gridGutterSize / 2;\n        }\n    }\n}\n\n.flexgrid--column-reversed {\n    flex-direction: column-reverse;\n    flex-flow: column-reverse;\n}\n\n.flexgrid--row-reversed {\n    flex-direction: row-reverse;\n}\n\n.flexgrid--mobile-xl-row {\n    @include min-mobile-xl {\n        flex-direction: row;\n    }\n}\n\n.flexgrid--justify-space-between {\n    @extend %disable-flex-grow;\n    justify-content: space-between;\n}\n\n.flexgrid--justify-space-around {\n    @extend %disable-flex-grow;\n    justify-content: space-around;\n}\n\n.flexgrid--justify-space-evenly {\n    @extend %disable-flex-grow;\n    justify-content: space-evenly;\n}\n\n.flexgrid--justify-right {\n    @extend %disable-flex-grow;\n    justify-content: flex-end;\n}\n",".card {\n    @extend %reset-last-margin;\n    max-width: 600px;\n\n    @include max-tablet() {\n        margin-bottom: $buildingUnit * 4;\n    }\n}\n\n.card__visual {\n    margin: 0 auto ($buildingUnit * 1.5);\n    overflow: hidden;\n\n    @include min-mobile-xl {\n        margin: 0 auto ($buildingUnit * 2);\n    }\n}\n\n.card__icon,\n.card__image{\n    width: 100%;\n}\n\n.card__icon {\n    height: 100%;\n}\n\n.card__content {\n    @extend %reset-last-margin;\n    width:100%;\n}\n\n.card__headline {\n}\n\n.card__description {\n}\n\n.card__link {\n}\n","$triangleSize: $buildingUnit;\n\n.tool