ted {\n        color: $legalColor;\n    }\n\n    .link__underline {\n        background-image: linear-gradient(transparent calc(100% - 1px), $legalColor 10px);\n    }\n\n}\n","$scrollHintImageMargin: 1em / 3;\n\n.scroll-hint {\n    user-select: none;\n    position: relative;\n\n    &__icon--left,\n    &__icon--right,\n    &__icon--bottom {\n        vertical-align: middle;\n        width: 1em;\n        height: 1em;\n    }\n    &__icon--left {\n        margin-right: $scrollHintImageMargin;\n    }\n    &__icon--right {\n        margin-left: $scrollHintImageMargin;\n        transform: rotate(180deg);\n    }\n    &__icon--bottom {\n        position: absolute;\n        display: block;\n        transform: rotate(-90deg);\n        left: 50%;\n    }\n}\n","$maxColCountMob: 4;\n$gutterMob: $gridGutterSizeMobile * #{$maxColCountMob - 1};\n$padMob: $gridGutterSizeMobile;\n\n$maxColCount: 5;\n$gutter: $gridGutterSize * #{$maxColCount - 1};\n$pad: $gridGutterSize;\n\n.flyout {\n    @extend %reset-list;\n    display: flex;\n    flex-direction: column;\n\n    padding: $buildingUnit;\n    padding-bottom: $buildingUnit * 2;\n\n    &:last-child {\n        margin-bottom: -6px;\n\n        .flyout__link--cta-mini {\n            @include max-tablet {\n                margin-bottom: 6px;\n            }\n        }\n    }\n\n    @include min-mobile-xl {\n        flex-direction: row;\n    }\n\n    //@include min-desktop {\n    .main-nav--large & {\n        position: absolute;\n        left: 0;\n        right: 0;\n        padding: ($buildingUnit * 2) $buildingUnit;\n\n        border-bottom: 1px solid $colorGray;\n        background: $colorWhite;\n\n        @include min-desktop-hd {\n            padding: ($buildingUnit * 2);\n        }\n    }\n}\n\n.flyout__section {\n    @extend %reset-list-item;\n    @extend %reset-last-margin;\n    background: transparent;\n    margin-bottom: 23px;\n\n    &:last-child {\n        margin-bottom: 0;\n    }\n\n    @include min-mobile-xl {\n        width: calc((25% - #{$gutterMob / $maxColCountMob}));\n\n        margin-bottom: 0;\n        padding-right: $padMob;\n\n        &:not(:last-child) {\n            margin-right: $gridGutterSizeMobile;\n            border-right: 1px solid $colorGrayLight;\n        }\n    }\n\n    //@include min-desktop {\n    .main-nav--large & {\n        width: calc((20% - #{$gutter / $maxColCount}));\n        padding-right: $pad;\n\n        &:not(:last-child) {\n            margin-right: $gridGutterSize;\n        }\n    }\n}\n\n.flyout__section--2-columns {\n    //@include min-desktop {\n    .main-nav--large & {\n        width: calc(((20% - #{$gutter / $maxColCount}) * 2) + #{$pad});\n\n        & > .flyout__list {\n            @extend .link-list--2-columns;\n        }\n    }\n}\n\n.flyout__section--3-columns {\n    //@include min-desktop {\n    .main-nav--large & {\n        width: calc(((20% - #{$gutter / $maxColCount}) * 3) + #{$pad * 2});\n\n        & > .flyout__list {\n            @extend .link-list--3-columns;\n        }\n    }\n}\n\n.flyout__title {\n    @extend .link-list__headline;\n    color: initial;\n}\n\n.flyout__list {\n    @extend .link-list;\n\n    //@include min-desktop {\n    .main-nav--large & {\n        margin-bottom: 0;\n    }\n\n    & + .flyout__link--cta-mini {\n        //@include min-desktop {\n        .main-nav--large & {\n            margin-top: 21px;\n        }\n    }\n}\n\n.flyout__list-item {\n    background: transparent;\n    @extend .link-list__item;\n    @extend %reset-last-margin;\n\n    &--collapsed-sm {\n        @include visually-hidden;\n\n        //@include min-tablet() {\n        .main-nav--large & {\n            @include reset-visually-hidden;\n        }\n    }\n    &--expanded {\n        @include reset-visually-hidden;\n    }\n}\n\n.flyout__link {\n    @extend .link--reversed;\n    color: initial;\n    &:hover {\n        color: initial;\n    }\n    margin-right: 0;\n\n    &--cta-mini {\n        display: table;\n    }\n}\n\n.flyout__toggle {\n    //@include min-desktop() {\n    .main-nav--large & {\n        @include visually-hidden;\n    }\n}\n","// Device breakpoints\n$gridNumberOfCols: 12;\n$gridBreakpoints: (\n    'mobile': (\n        selector: 'xxs',\n        minWidth: 320px\n    ),\n    'mobile-xl': (\n        selector: 'xs',\n        minWidth: 569px,\n        maxWidth: 767px\n    ),\n    'tablet': (\n        selector: 'sm',\n        minWidth: 768px,\n        maxWidth: 1024px\n    ),\n    'desktop': (\n        selector: 'md',\n        minWidth: 1025px,\n        maxWidth: 1200px\n    ),\n    'desktop-hd': (\n        selector: 'lg',\n        minWidth: 1201px\n    )\n);\n$gridGutterSizeMobile: $buildingUnit;\n$gridGutterSize: $buildingUnit * 2;\n\n$mobileWidth:                   map-get(map-get($gridBreakpoints, mobile), minWidth);\n$mobileLWidth:                  480px;\n$mobileXlWidth:                 map-get(map-get($gridBreakpoints, mobile-xl), minWidth);\n$tabletWidth:                   map-get(map-get($gridBreakpoints, tablet), minWidth);\n$desktopWidth:                  map-get(map-get($gridBreakpoints, desktop), minWidth);\n$desktopHdWidth:                map-get(map-get($gridBreakpoints, desktop-hd), minWidth);\n","$colorNavItemActive: darken($colorWhite, $tintActive);\n$mainNavBorder: 1px solid $colorGray;\n\n.main-nav {\n    position: relative;\n\n    &--small {\n        flex: 0 1 auto;\n        order: 30;\n        z-index: 0;\n\n        &:before {\n            transform: translateX(-100vw);\n            content: '';\n            height: calc(100vh - #{$navHeight});\n\n            position: absolute;\n            top: $navHeight;\n            left: 0;\n            right: 0;\n\n            opacity: 0;\n            background-color: $colorBlack;\n\n            transition: opacity 0.5s ease(easeOutQuint), transform 0s 0.5s ease(easeOutQuint);\n        }\n    }\n\n    &--large {\n        order: 20;\n        flex: 1 1 auto;\n        margin-right: $navHeight;\n\n        &:before {\n            content: none;\n            display: none;\n        }\n\n\n    }\n}\n\n.main-nav--large,\n.main-nav--large .main-nav__list,\n.main-nav--large .main-nav__item {\n    display: flex;\n    height: 100%;\n    align-items: center;\n}\n\n.main-nav__nav-assist {\n    position: absolute;\n    top: 0;\n    right: 0;\n\n    .main-nav--large & {\n        @include min-desktop{\n            display: none;\n        }\n    }\n}\n\n.main-nav__list {\n    @extend %reset-list;\n\n    .main-nav--small & {\n        top: 100%;\n        left: 0;\n        max-height: calc(100vh - #{ $navHeight });\n\n        border-right: $mainNavBorder;\n        border-bottom: $mainNavBorder;\n\n        width: calc(100% - #{ $navHeight });\n        overflow-y: scroll;\n\n        background-color: $colorWhite;\n    }\n\n    .main-nav--large & {\n        position: static;\n        margin-top: ($buildingUnit / 2) +1;\n        border-right: 0;\n\n        width: auto;\n        overflow-y: unset;\n\n        opacity: 1;\n        background-color: transparent;\n    }\n}\n\n.main-nav__item {\n    .main-nav--small & {\n        padding: 0;\n        margin-bottom: 0;\n\n        border-bottom: $mainNavBorder;\n\n        &:last-child {\n            border: none;\n        }\n    }\n\n    .main-nav--large & {\n        display: flex;\n        flex-direction: row;\n\n        padding: 0 ($buildingUnit / 2);\n        margin-bottom: 6px;\n\n        border-right: 0;\n        border-bottom: 0;\n\n        &:last-of-type {\n            margin-right: 0;\n        }\n\n        @include min-desktop-hd {\n            padding: 0 ($buildingUnit * 1.25);\n        }\n    }\n}\n\n.main-nav__link {\n    display: block;\n\n    z-index:2;\n    cursor: pointer;\n\n    padding: 19px $buildingUnit 17px;\n    position: relative;\n\n    color: $colorBlack;\n\n    @extend %fontSecondary;\n    @include font-line-ratio(sec-xxs);\n    @include micro-animate(background-color);\n\n\n    &:hover{\n        color: $colorBlack;\n    }\n\n    &:focus:not(:hover),\n    &:focus:not